/**
 * Group reservations by date and sort them
 * @param reservationList - Array of reservations
 * @returns Grouped and sorted reservations
 */
export const groupReservationsByDate = (reservationList) => {
    if (!reservationList || reservationList.length === 0)
        return [];
    const groups = {};
    reservationList.forEach((reservation) => {
        const date = reservation.date;
        if (!groups[date]) {
            groups[date] = [];
        }
        groups[date].push(reservation);
    });
    // Convert to array and sort by date using date-fns
    return Object.entries(groups)
        .map(([date, reservations]) => ({
        date,
        reservations,
        count: reservations.length,
    }))
        .sort((a, b) => {
        // Sort by date (newest first)
        try {
            const dateA = parse(a.date, "dd-MM-yyyy", new Date());
            const dateB = parse(b.date, "dd-MM-yyyy", new Date());
            if (isValid(dateA) && isValid(dateB)) {
                return dateB.getTime() - dateA.getTime();
            }
            // Fallback: string comparison
            return b.date.localeCompare(a.date);
        }
        catch (_a) {
            return b.date.localeCompare(a.date);
        }
    });
};
