/**
 * Calculate selection state for a reservation group
 * @param group - Grouped reservation data
 * @param selectedRows - Array of selected row IDs
 * @returns Selection state information
 */
export const getGroupSelectionState = (group, selectedRows) => {
    const groupIds = group.reservations.map((r) => r.id);
    const selectedInGroup = groupIds.filter((id) => selectedRows.includes(id));
    const selectedCount = selectedInGroup.length;
    const totalCount = groupIds.length;
    const isAllSelected = totalCount > 0 && selectedCount === totalCount;
    return { selectedCount, isAllSelected };
};
/**
 * Handle individual row selection
 * @param record - Reservation record
 * @param selected - Whether the row is selected
 * @param selectedRows - Current selected rows
 * @param onSelectionChange - Callback to update selection
 */
export const handleRowSelection = (record, selected, selectedRows, onSelectionChange) => {
    const currentKeys = selectedRows || [];
    let newSelectedKeys;
    if (selected) {
        newSelectedKeys = [...currentKeys, record.id];
    }
    else {
        newSelectedKeys = currentKeys.filter((k) => k !== record.id);
    }
    onSelectionChange(newSelectedKeys);
};
/**
 * Handle group selection (select/deselect all items in a group)
 * @param group - Grouped reservation data
 * @param selected - Whether the group should be selected
 * @param selectedRows - Current selected rows
 * @param onSelectionChange - Callback to update selection
 */
export const handleGroupSelection = (group, selected, selectedRows, onSelectionChange) => {
    const groupIds = group.reservations.map((r) => r.id);
    const currentKeys = selectedRows || [];
    let newSelectedKeys;
    if (selected) {
        // Add all group IDs that aren't already selected
        const newIds = groupIds.filter((id) => !currentKeys.includes(id));
        newSelectedKeys = [...currentKeys, ...newIds];
    }
    else {
        // Remove all group IDs
        newSelectedKeys = currentKeys.filter((id) => !groupIds.includes(id));
    }
    onSelectionChange(newSelectedKeys);
};
/**
 * Handle master selection (select/deselect all items)
 * @param checked - Whether all items should be selected
 * @param reservationList - All reservations
 * @param onSelectionChange - Callback to update selection
 */
export const handleMasterSelection = (checked, reservationList, onSelectionChange) => {
    if (checked) {
        onSelectionChange(reservationList.map((r) => r.id));
    }
    else {
        onSelectionChange([]);
    }
};
/**
 * Calculate master selection state
 * @param selectedRows - Current selected rows
 * @param reservationList - All reservations
 * @returns Whether master checkbox should be checked
 */
export const getMasterSelectionState = (selectedRows, reservationList) => {
    return (selectedRows.length > 0 &&
        selectedRows.length === reservationList.length);
};
