/**
 * External Dependencies
 */
import { format, addMonths } from "date-fns";
/**
 * Get default date range for reservation system
 * Returns today's date as start and 15th of next month as end
 */
export const getDefaultDateRange = () => {
    const today = new Date();
    const nextMonth = addMonths(today, 1);
    const fifteenthOfNextMonth = new Date(nextMonth.getFullYear(), nextMonth.getMonth(), 15);
    return {
        startDate: format(today, "yyyy-MM-dd"),
        endDate: format(fifteenthOfNextMonth, "yyyy-MM-dd"),
    };
};
